import { Heading, SimpleGrid, Stack } from '@chakra-ui/layout'
import { Box } from '@material-ui/core'
import React from 'react'
import Project from './Project'

export default function ProjectsStack() {
    return (
        <Stack
        direction='column'
        align='center'
        w='100%'
        pt={24}
        spacing={12}
        >
            <Heading>Projects</Heading>
            <SimpleGrid columns={[1, 1, 3, 3]} gap={12}>
                <Project title="Why is this empty?">
                    I am still working on this website!
                </Project>
                <Project title="Why is this empty?">
                    I am still working on this website!
                </Project>
                <Project title="Why is this empty?">
                    I am still working on this website!
                </Project>
            </SimpleGrid>
            <Stack
            borderWidth={1}
            borderColor={'gray.100'}
            p={4}
            direction='column'
            align='center'
            borderRadius={16}
            shadow="base"
            spacing={12}
            >

            </Stack>
        </Stack>
    )
}
