import { Box, Flex, Stack } from '@chakra-ui/layout'
import React from 'react'

export default function NavbarContainer(props) {
    return (
        <Flex
        as='nav'
        justify='center'
        w='100%'
        borderWidth={1}
        borderColor='gray.100'
        p={6}
        bg='white'
        >
            <Stack
            w='60%'
            direction='row'
            spacing={12}
            justify='space-evenly'
            >
            {props.children}
            </Stack>
        </Flex>
    )
}
