import { Image } from '@chakra-ui/image'
import { Box, Flex, Heading, Stack, Text } from '@chakra-ui/layout'
import React, { forwardRef } from 'react'
import TTWord from './TTWord'

function AboutStackInner(props, ref) {
    return (
        <Flex
        w={['100%', '70%', '50%', '40%']}
        pt={24}
        ref = {ref}
        align='center'
        flexDirection='column'
        >
            <Stack
            borderWidth={1}
            borderColor={'gray.100'}
            p={4}
            direction='column'
            align='center'
            borderRadius={16}
            shadow="base"
            spacing={12}
            >
                <Heading textAlign='center'>Who am I?</Heading>
                <Image borderRadius='full' src={process.env.PUBLIC_URL + '/me_cropped.png'} w={[40, 48, 64]}/>
                <Text textAlign='center' as='b'>
                    Hello! My name is Efe Akinci.
                </Text>
                <Text textAlign='center' as='p'>I am currently a senior at Skyline High School, and I am planning on majoring in <TTWord label="computer science">and maybe also math? or perhaps dual major in cs and business? or maybe a minor in math and a dual-major in business and cs? is that even possible?</TTWord>. I work with a research team at Michigan Medicine to apply machine learning and other comptuer science concepts to rare-diseases.</Text>
                <Text textAlign='center' as='p'>I also really enjoy doing robotics. In 2018-2019, I was the Engineering Projects lead for Team 6429 where I developed social media apps and websites, <TTWord label='scouting'>This is a thing in robotics where we collect data on other teams' robots to better choose teammates.</TTWord> programs, edited videos, and more.</Text>
            </Stack>
        </Flex>
    )
}

const AboutStack = forwardRef(AboutStackInner);

export default AboutStack;
