import { Heading, Stack, Text } from '@chakra-ui/layout'
import React from 'react'

export default function Project(props) {
    return (
        <Stack
        borderWidth={1}
        borderColor={'gray.100'}
        p={4}
        direction='column'
        align='center'
        borderRadius={16}
        shadow="base"
        spacing={12}
        >
            <Heading textAlign='center'>{props.title}</Heading>
            <Text>{props.children}</Text>
        </Stack>
    )
}
